.customer-contact__wrapper {
  display: flex;
  flex-direction: column;
}

.customer-contact__input {
  display: flex;
}

.customer-contact__input-email {
  flex-grow: 1;
}

.customer-contact__input-tel {
  flex-grow: 1;
}

.customer-contact__title {
  font-family: "oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  font-size: 24px;
  line-height: 26px;
  color: rgba(0, 0, 0, 1);
  text-transform: none;
}
